import {DropzoneContainer, DropzoneText} from "./style";
import {UploadCloud} from 'react-feather'
import {FC, useEffect} from "react";
import {Container, InvisibleInput, Row} from "../signup-form/style";
import {useDropzone} from 'react-dropzone'

interface DropZoneProps{
    photoRef?:any,
    many?:boolean,
    setimages?:any
}


let Dropzone:FC<DropZoneProps> = (props) =>{

    const {
        getRootProps,
        getInputProps,
        acceptedFiles
    } = useDropzone({
        accept: '.jpeg,.png,.svg,.jpg'
    });
    console.log(acceptedFiles)
   useEffect(()=>{
       props.setimages((prevSt:any)=>[...prevSt,...acceptedFiles])
   },[acceptedFiles])
    return(
        <DropzoneContainer type='button' {...getRootProps()}>
            <InvisibleInput type='file' {...getInputProps()}   />
            <Container>
                <Row md='100' lg='100' xl='100'>
                    <UploadCloud size={50} color='#c0ccda'/>
                </Row>
                <Row md='100' lg='100' xl='100'>
                    <DropzoneText>Upload {props.many ? 'photos' :'a photo'}</DropzoneText>
                </Row>
            </Container>
        </DropzoneContainer>
    )
}
export default Dropzone